import React, { useEffect, useState } from "react";
import { useRef } from "react";
import ApiHeader from '../../../../../Components/ApiList/ApiHeader'
import BarLoader from '../../../../../Components/Common/BarLoader'
import { toast, ToastContainer } from 'react-toastify'
import axios from 'axios'
import ReactToPrint from "react-to-print-advanced";
import FamReciept from "./FamReciept";
import { useParams } from "react-router-dom";
import PropertyApiList from '../../../../../Components/ApiList/PropertyApiList'
import BrandLoader from "../../../../../Components/Common/BrandLoader";
import CommonModal from "../../../../../Components/GlobalData/CommonModal";
import ServerErrorCard from "../../../../AdvertisementModule/ServerErrorCard";
import { AiFillPrinter } from "react-icons/ai";
import { buttonActionSolid } from "../../../../../Components/Common/Styles/Styles";
import AxiosInterceptors from "../../../../../Components/GlobalData/AxiosInterceptors";
import ulb_data from "../../../../../Components/Common/DynamicData";

const FamPrintPage = () => {
  const componentRef = useRef();

  const id = useParams()

  const {api_samReciept} = PropertyApiList()

  const [loader, setloader] = useState(false)
  const [famDetails, setfamDetails] = useState()
  const [erroState, seterroState] = useState(false);
  const [errorMsg, seterrorMsg] = useState('')

  useEffect(() => {

      setTimeout(() => {
          setloader(false)
      }, 10000);

      setloader(true)

      AxiosInterceptors.post(api_samReciept, {memoId : id?.id}, ApiHeader())
      .then((res) => {
          console.log('getting fam memo details => ', res)
          setloader(false)
          setfamDetails(res?.data?.data)
          seterrorMsg(res?.data?.message)
      })
      .catch((err) => {
          console.log("getting fam memo error => ", err)
          setloader(false)
         seterroState(true)
      })
  },[])

  if (loader) {
    return (
      <>
        <BrandLoader />
      </>
    )
  }
  if (erroState) {
    return (
      // <CommonModal>
        <ServerErrorCard title="Server is busy" desc={errorMsg} buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
      // </CommonModal>
    )
  }

return (
   <div>

<ToastContainer position="top-right" autoClose={2000} />
      <ReactToPrint
        trigger={() => <div className='z-10 fixed bottom-10 text-center  justify-center items-center  w-screen'>
        <button className={buttonActionSolid}>
            <AiFillPrinter className='inline text-lg' />
            print
        </button>
    </div>}
        content={() => componentRef.current}
      />
      <FamReciept ref={componentRef} famDetails={famDetails} ulbData={ulb_data()}/>
    </div>
  );
};

export default FamPrintPage;
